.IntroSection {
  height: 200vh;
}
.introSpace {
  position: sticky;
  top: 0;
  width: 100%;
  height: 100vh;
}
.phase2 {
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%);
  transition: 1s;
  z-index: 100;
}
h2.secondTitleText {
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 20px;
}

h2.TitleText {
  width: max-content;
  position: absolute;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  font-size: 70px;
  font-weight: 800;
  transition: 0.5s;
  z-index: 100;
}
h3.titleComent {
  font-size: 24px;
  margin-bottom: 30px;
}
@media (max-width:1440px) {
  h2.TitleText{
    font-size: 50px;
  }
  h2.secondTitleText {
    font-size: 32px;
    font-weight: 700;
  }
  h3.titleComent {
    font-size: 16px;
  }
}
